export const checkDKS = (barcode) => {
  if (barcode.length === 11) {
    let GW = barcode.split("#")[0];
    let NW = barcode.split("#")[1];
    let PKG = "NW - GW";
    // console.log(GW);
    var decimal_gw =
      parseInt(GW.substr(0, 3)) + "." + GW.substr(GW.length - 2, 2);
    var decimal_nw =
      parseInt(NW.substr(0, 3)) + "." + NW.substr(NW.length - 2, 2);
    console.log(decimal_gw + "-" + decimal_nw);
    let data = {
      NW: NW,
      PKG: PKG,
      GW: GW,
    };
    return data;
  } else {
    return false;
  }
};
